<template>
  <div class="bottom-bar">
    <check-botton class="check-botton" ref="CheckBotton" @click.native="checkAllClick" :isActive="isSelectAll" />
    <div class="text">
      <span>全选</span>
      <span class="price">合计：{{ totalPrice }}</span>
      <span class="number" @click="computClick">去计算：({{ getActiveCount }})</span>
    </div>
  </div>
</template>

<script>
import CheckBotton from "./CheckBotton";
import { mapState, totalPrice, mapGetters } from "vuex";
import CartListVue from './CartList.vue';

export default {
  components: {
    CheckBotton
  },
  computed: {
    ...mapState(['cartList']),
    ...mapGetters(['totalPrice', 'getActiveCount']),
    isSelectAll() {
      return this.cartList.length ? (!this.cartList.find(item => {
        return !item.isActive;
      })) : false;
    }
  },
  methods: {
    checkAllClick() {
      if (!this.isSelectAll) {  // 激活全部
        this.$store.commit('chgeAllActive');
      } else {  // 全部不激活
        this.$store.commit('unChgeAllActive');
      }
    },
    computClick() {
      this.$toast.show('正在计算商品', 1500);
    }
  }
}
</script>

<style scoped>
  .bottom-bar {
    position: fixed;
    display: flex;
    z-index: 9;
    left: 0;
    right: 0;
    bottom: 49px;
    height: 2rem;
    align-items: center;
    padding-left: 0.5rem;
    background-color: #fff;
    border-top: 0.05rem solid #eee;
  }

  .bottom-bar .text {
    display: flex;
    flex: 1;
    height: 2rem;
    line-height: 2rem;
    font-size: 0.8rem;
    margin-left: 0.25rem;
    justify-content: space-between;
  }


  .number {
    width: 100px;
    text-align: center;
    color: #fff;
    background-color: #f00;
  }
</style>
