<template>
  <div class="cart-item" ref="cartItem" @touchstart="itemStart" >
    <div class="tres-left" @touchstart="itemStart">
      <div class="check-wrap" @click="checkItemClick">
        <check-botton ref="CheckBotton" :isActive="isSelectCart"/>
      </div>
      <div>
        <img @touchstart="itemStart" class="tres-img" :src="getCart.topImages" alt="">
      </div>
    </div>
    <div class="tres-detail" ref="tres" @touchstart="itemStart">
      <h2 class="title">{{ getCart.title }}</h2>
      <p class="desc">{{ getCart.desc }}</p>
      <div class="cart-info">
        <span class="price">￥{{ getCart.newPrice }}</span>
        <span class="count" v-text="'x' + getCart.count"></span>
      </div>
    </div>
  </div>
</template>

<script>
import CheckBotton from "./CheckBotton";
import { mapState } from "vuex";
import CartListVue from './CartList.vue';

import { moveBox } from "common/utils";

export default {
  components: {
    CheckBotton
  },
  props: {
    iid: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      x: 0
    }
  },
  computed: {
    ...mapState(['cartList']),
    getCart() {
      return this.cartList.find(item => {
        return item.iid === this.iid;
      });
    },
    isSelectCart() {
      return this.getCart.isActive;
    }
  },
  mounted() {
    this.$refs.tres.addEventListener('click', this.shopClick);
  },
  methods: {
    checkItemClick() {
      this.$store.commit('changeActive', this.getCart.iid);
    },
    shopClick() {
      this.$router.push('/detail/' + this.iid);
    },
    itemStart(e) {
      let el = this.$refs.cartItem;
      if (el.style.left === '-60px') {
        el.removeEventListener('touchmove', this.itemMove);
        el.removeEventListener('touchend', this.itemEnd);
        this.$refs.tres.removeEventListener('click', this.shopClick);
        el.style.left = '0px';
        el.style.transition = '0.3s';
        setTimeout(() => {
          el.style.transition = '';
          this.$refs.tres.addEventListener('click', this.shopClick);
        }, 300);
      } else {
        this.x = e.touches[0].screenX;
        el.addEventListener('touchmove', this.itemMove);
      }
    },
    itemMove(e) {
      var el = this.$refs.cartItem;
      var nowX = e.touches[0].screenX;
      var moveX = nowX - this.x;
      
      if (moveX < 0 && moveX > -120) {
        el.style.left = moveX + 'px';
        el.addEventListener('touchend', this.itemEnd);
      }
    },
    itemEnd(e) {
      var el = this.$refs.cartItem;
      var endX = parseInt(el.style.left.replace('px', '')); // -60
      if (endX > -30) {
        el.style.left = 0
      } else {
        el.style.left = '-60px';
        this.$refs.tres.removeEventListener('click', this.shopClick);
      }
      el.style.transition = '0.3s'
      setTimeout(() => {
        el.style.transition = '';
      }, 300)
    }
  }
}
</script>

<style scoped>
  .cart-item {
    position: relative;
    display: flex;
    background-color: #fff;
    z-index: 1;
    padding: 0.5rem;
  }

  .tres-left {
    display: flex;
    align-items: center;
  }
  
  .check-wrap {
    display: flex;
    align-items: center;
    height: 5rem;
  }

  .tres-left .tres-img {
    display: block;
    width: 80px;
    height: 120px;
    margin-left: 0.5rem;
    border-radius: 5px;
  }

  .tres-detail {
    flex: 1;
    margin-left: 1rem;
    font-size: 15px;
    overflow: hidden;
  }

  .tres-detail .title {
    font-size: 17px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    margin-bottom: 1rem;
  }

  .tres-detail .desc {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    margin-bottom: 2rem;
  }

  .cart-info {
    display: flex;
    justify-content: space-between;
  }

  .cart-info .price {
    color: #ff8040;
  }
</style>
