<template>
  <scroll class="scroll-content">
    <ul>
      <li v-for="item in cartList" class="item">
        <cart-item :iid="item.iid" ref="cartItem" :key="item.iid" />
        <div class="dele-btn" @click="deleClick(item.iid)">删除</div>
      </li>
    </ul>
  </scroll>
</template>

<script>
import { mapState } from 'vuex';
import CartItem from "./CartItem";
import Scroll from "components/common/scroll/Scroll";


export default {
  components: {
    CartItem,
    Scroll
  },
  computed: {
    ...mapState(['cartList'])
  },
  methods: {
    deleClick(id) {
      this.$store.commit('deletCart', id);
    }
  }
}
</script>

<style scoped>
  .scroll-content {
    height: calc(100% - 4.65rem);
  }

  .item {
    position: relative;
    box-sizing: border-box;
    border-bottom: 0.05rem solid #eee;
  }

  .dele-btn {
    position: absolute;
    width: 60px;
    height: 7rem;
    background-color: #f00;
    top: 0;
    right: 0;
    color: #fff;
    text-align: center;
    line-height: 7rem;
  }
</style>
